import React from "react";

import { Container, Row, Col } from 'reactstrap';

export default class WorkExperience extends React.Component {
    constructor(props) {
        super();
    }
  

    render() {
        const stringStyles = {
          
            textAlign: "left",
            listStyleType: "circle"
        };
        const { id,  startDate, endDate, orgName, title, role, Responsbilities } = this.props;

        return (
            <Row id={"work" + id}>
                <Col>
                <Row>
                        <Col style={stringStyles}>
                            {startDate}-{endDate}
                        </Col>
                </Row>
                    <Row>
                        <Col className="title" style={stringStyles}>
                            {title}
                            </Col>
                </Row>
                    <Row>
                        <Col className="title" style={stringStyles}>
                            {orgName} - {role}
                            </Col>
                </Row>
             
                 
       
                    <ul style={stringStyles}>   
                        {Responsbilities.map(resopn => {
                            return <li style={stringStyles}>{resopn}</li>;
        })}
                       
                   
                    </ul>
           
                </Col>
     </Row>
        );
    }
}
    